@import url(https://fonts.googleapis.com/css?family=Numans);
/* Made with love by Mutiullah Samim*/

html,body{
background-image: url('http://getwallpapers.com/wallpaper/full/a/5/d/544750.jpg');
background-size: cover;
background-repeat: no-repeat;
font-family: 'Numans', sans-serif;
}

a {
    color: white;
}

.container{
align-content: center;
}

.top{
  margin-top: 5%;
  margin-left: 2%;
}


.btn-primary{
    margin-right: 5px;
    height: 6vh;
}

.card{
margin-top: auto;
margin-bottom: auto;
width: 400px;
background-color: rgba(0,0,0,0.5) !important;
}

.social_icon span{
font-size: 60px;
margin-left: 10px;
color: #FFC312;
}

.social_icon span:hover{
color: white;
cursor: pointer;
}

.card-header h3{
color: white;
}

.social_icon{
position: absolute;
right: 20px;
}

.btn-primary-outline {
  background-color: transparent;
  border-color: transparent;
  color: white;

}


.input-group-prepend span{
width: 50px;
background-color: #FFC312;
color: black;
border:0 !important;
}

input:focus{
outline: 0 0 0 0  !important;
box-shadow: 0 0 0 0 !important;

}

.remember{
color: white;
}

.remember input
{
width: 20px;
margin-left: 15px;
margin-right: 5px;
}

.login_btn{
color: black;
background-color: #FFC312;
width: 100px;
}

.login_btn:hover{
color: black;
background-color: white;
}

.links{
color: white;
}

.links a{
margin-left: 4px;
}

label{
  color: white;
}

.normal {
}
.nothing {
  display: none;
}


.container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-flow: nowrap column;
    overflow:hidden;
    margin-left: 0px;
    margin-right: 0px;
    max-width: 5000px;
    padding-left: 0px;
    padding-right: 0px;
    margin-top: 4%;
}




.body {
    background: gray;
    flex-grow: 3;
    display: flex;
    flex-flow: row nowrap;
}


.RightSidebar {
    background: #4E8098;
    min-width: 250px;
    max-width: 5000px;
    box-sizing: border-box;
    text-align: center;
    flex-grow: 1;
}

.content {
    flex-grow: 2;
    background: white;
}

